@use "global/bootstrap";

.main-hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  justify-content: center;
  background-color: #81b3d28c;
  padding-top: 52px;
  padding-bottom: 67px;

  background-image: url("../img/main-hero/back-mobile@1x.webp");
  // для подключения изображений других форматов можно использовать image-set
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/main-hero/back-mobile@2x.webp");
  }

  @media (min-width: bootstrap.$tablet-width) {
    padding-top: 95px;
    padding-bottom: 113px;
    background-image: url("../img/main-hero/back-tablet@1x.webp");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/main-hero/back-tablet@2x.webp");
    }
  }

  @media (min-width: bootstrap.$desktop-width) {
    padding-top: 101px;
    padding-bottom: 120px;
    background-image: url("../img/main-hero/back-desktop@1x.webp");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/main-hero/back-desktop@2x.webp");
    }
  }

  &--index {
    padding-top: 90px;
    padding-bottom: 85px;

    @media (min-width: bootstrap.$tablet-width) {
      padding-top: 77px;
      padding-bottom: 91px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    background-image: url("../img/intro-triangle.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center bottom;
    width: 100%;
    bottom: -1px;
    aspect-ratio: 320 / 57;

    @media (min-width: bootstrap.$tablet-width) {
      background-image: url("../img/intro-triangle-tablet.svg");
      aspect-ratio: 768 / 57;
    }

    @media (min-width: bootstrap.$desktop-width) {
      background-image: url("../img/intro-triangle-desktop.svg");
    }
  }
}

.main-hero__image-index {
  @media (min-width: bootstrap.$tablet-width) {
    width: 461px;
    height: 350px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    width: 460px;
  }
}

.main-hero__image {
  @media (min-width: bootstrap.$tablet-width) {
    width: 460px;
    height: 80px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    height: 78px;
  }
}
