@use "global/bootstrap";

.form {
  width: 100%;
}

.form__wrapper-fieldset {
  @media (min-width: bootstrap.$tablet-width) {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 60px;
    margin-bottom: 63px;
    border-top: none;
    gap: 58px;

    @media (min-width: bootstrap.$desktop-width) {
      gap: 100px;
      padding: 0 120px;
      margin-bottom: 71px;
    }
  }
}

.form__wrapper-fieldset::before {
  @media (min-width: bootstrap.$tablet-width) {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background-color: #dbdbdb;
    background-repeat: no-repeat;
    bottom: -14px;
    left: 50%;
  }
}

.form__fieldset {
  padding: 24px 20px 37px;
  border-top: 1px solid #e5e5e5;
  min-width: 0;

  @media (min-width: bootstrap.$tablet-width) {
    padding: 0;
    border-top: none;
  }

  &--with-img {
    padding-bottom: 37px;
    border: none;

    @media (min-width: bootstrap.$tablet-width) {
      padding-bottom: 0;
    }
  }

  &--switches {
    padding-bottom: 13px;

    @media (min-width: bootstrap.$tablet-width) {
      margin: 0 60px;
    }

    @media (min-width: bootstrap.$desktop-width) {
      margin: 0 120px;
    }
  }

  &--textarea {
    padding-bottom: 51px;
    border: none;

    @media (min-width: bootstrap.$tablet-width) {
      padding-bottom: 0;
      margin: 0 60px 43px;
      border-top: none;
    }

    @media (min-width: bootstrap.$desktop-width) {
      margin: 0 120px 49px;
      width: 960px;
    }
  }
}

.form__submit-wrapper {
  padding: 0 20px;
  display: flex;
  flex-direction: column;

  @media (min-width: bootstrap.$tablet-width) {
    padding: 0;
    margin: 0 60px;
    flex-direction: row;
  }

  @media (min-width: bootstrap.$desktop-width) {
    margin: 0 auto;
    width: 768px;
  }
}

.form__submit-button {
  @include bootstrap.button;
  width: 100%;
  padding: 16px 0;

  @media (min-width: bootstrap.$tablet-width) {
    width: 324px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    width: 384px;
  }
}

.form__postscript {
  text-transform: uppercase;
  font-size: 12px;
  color: rgba(51, 51, 51, 0.6);
  margin-left: auto;

  @media (min-width: bootstrap.$tablet-width) {
    align-self: center;
    color: #333333;
    font-size: 14px;
  }
}
